.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__link {
  text-transform: uppercase;
  text-decoration: none;
  color: $color-black;

  &:hover {
    opacity: 0.6;
  }

  &--down {
    opacity: 0.3;
  }

  // &--active {
  //   border-bottom: 2px solid $basic-green;
  // }
}

.main-nav--form {
  background: none;
}

.main-nav__link--form {
  color: $color-black;
}

@media (max-width: $tablet-width - 1px) {
  .main-nav__list {
    @include hidden;
  }

  .main-nav__toggle {
    display: block;
    margin-top: 10px;
  }
}

@media (min-width: $tablet-width) and (max-width: $desktop-width - 1px) {
  .main-nav__toggle {
    @include hidden;
  }

  .main-nav__link--active {
    position: relative;
  }

  .main-nav__link--active::after {
    content: "";
    position: absolute;
    background-color: $basic-green;
    display: block;
    width: 100%;
    height: 2px;
    bottom: -9px;
    left: -2px;
  }

  .main-nav__list {
    display: flex;
    margin-top: 18px;
  }

  .main-nav__item {
    padding-left: 36px;
  }
}

@media (min-width: $desktop-width) {
  .main-nav__link {
    color: $color-black;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
  }

  .main-nav__toggle {
    @include hidden;
  }

  .main-nav__list {
    display: flex;
    margin-top: 16px;
    margin-bottom: 3px;
  }

  .main-nav__item {
    padding-left: 35px;
  }

  .main-nav__link--index {
    color: $color-white;
    border-bottom: 2px solid $color-white;
  }
}
